.special {
    text-align: center;
    max-width: 120rem;
    width: 80%;
    margin: 4rem auto;

    h2 {
        font-family: "Noticia Text", 'sans-serif';
        font-weight: 400;
        font-size: 4.5rem;
        color: #000000;

        margin: 3rem 0 7rem;
    }

    &-each {
        @include respond(tablet) {
            display: block;
            width: 100%;
            margin: 0 auto 4rem;
        }

        width: 90%;
        margin: 0 auto;
        text-align: left;
        @include respond(tablet) {
            text-align: center;
        }
        img {
           
            height: 20rem;
            width: 20rem;
            border-radius: 100%;
        }

        &-content {
            text-align: left;
            @include respond(tablet) {
                display: block;
                width: 100%;
            }

            width: 70%;
        }

        justify-content: space-between;
        align-items: center;
        display: flex;
    }

    h3 {
        font-family: 'Noticia Text', 'sans-serif';
        font-weight: 300;
        font-size: 3.2rem;
        line-height: 4.5rem;
        color: #111111;
        margin: 3rem 0 0rem;
    }

    p {
        font-family: Questrial, 'sans-serif';
        font-weight: 400;
        font-size: 1.7rem;
        line-height: 3.4rem;
        color: #212529;
        margin: 3rem 0 7rem;
    }


    text-align:center;


}